import React from 'react';
import Base from 'base';
import {Link} from 'react-router';
import BreadCrumb from 'admin/component/breadcrumb';
import moment from 'moment';

import PostAction from 'admin/action/post';
import PostStore from 'admin/store/post';
import SystemAction from 'admin/action/system';
import SystemStore from 'admin/store/system';

export default class extends Base {
  state = {
    platform: 'Linux',
    nodeVersion: '4.2',
    v8Version: '1.1',
    mysqlVersion: 'xxx',
    thinkjsVersion: '2.1',
    firekylinVersion: '2.0',
    posts: [],
    count: {
      posts: 0,
      comments: 0,
      cates: 0
    }
  };

  componentWillMount() {
    this.listenTo(PostStore, posts => this.setState({posts}));
    this.listenTo(SystemStore, data => {
      this.setState(Object.assign({}, data.versions, {count: data.count}));
    });
    PostAction.selectLastest();
    SystemAction.select();
  }

  render() {
    return (
      <div className="fk-content-wrap">
        <BreadCrumb {...this.props} />
        <div className="manage-container">
          <h3 style={{marginBottom: '30px'}}></h3>
          <p> {this.state.count.posts} ,  {this.state.count.comments}  {this.state.count.cates} . </p>
          <p>:</p>
          <div className="">
            <Link to="/post/create"></Link>
            <Link to="/options/general" style={{marginLeft: 20}}></Link>
          </div>
          <hr />
          <div className="row">
            <div className="col-md-4">
              <h4></h4>
              <ul>
                {this.state.posts.map(post =>
                  <li key={post.id}>
                    <label>{moment(new Date(post.create_time)).format('MM.DD')}</label>
                    <a href={`/post/${post.pathname}`} target="_blank">{post.title}</a>
                  </li>
                )}
              </ul>
            </div>
            <div className="col-md-4">
              <h4></h4>
              <ul>
                <li><label></label>{this.state.platform}</li>
                <li><label>Node.js</label>{this.state.nodeVersion}</li>
                <li><label>V8</label>{this.state.v8Version}</li>
                <li><label>MySQL</label>{this.state.mysqlVersion}</li>
                <li><label>ThinkJS</label>{this.state.thinkjsVersion}</li>
                <li><label>FireKylin</label>{this.state.firekylinVersion}</li>
              </ul>
            </div>
            <div className="col-md-4">
              <h4></h4>
              <ul>
                <li>
                  <label></label>
                  <a href="http://firekylin.org/" target="_blank">http://firekylin.org/</a>
                </li>
                <li>
                  <label></label>
                  <a href="https://github.com/welefen/firekylin">https://github.com/welefen/firekylin</a>
                </li>
                <li>
                  <label></label>
                  <a href="https://github.com/welefen/firekylin/issues">https://github.com/welefen/firekylin/issues</a>
                </li>
                <li>
                  <label></label>
                  <a href="http://www.75team.com/">http://www.75team.com/</a>
                </li>
                <li>
                  <label></label>
                  <a href="https://github.com/welefen">Welefen</a><a href="https://github.com/lizheming">lizheming</a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}



